<script>
export default {
  name: 'layout',
  data() {
    return {
      crumbs: [],
    };
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo;
    },
    menuObj() {
      return this.$store.state.menuObj;
    },
  },
  watch: {
    $route: {
      handler(to) {
        this.defaultActive = to.path;
        this.crumbs = to.name !== 'pagenotfind' ? to.meta.crumbs : [];
      },
      immediate: true,
    },
  },
  methods: {
    logout() {
      this.$store.dispatch('LOGOUT').finally(() => {
        this.$router.replace('/login');
      });
    },
  },
};
</script>

